.cont{
    display: grid;
    grid-template-columns: 260px 1fr 1fr;
    grid-template-rows: 30px 1fr 0;
    grid-template-areas: 
    "header header header"
    "sidebar content content";
    width: 100%;
    
    
}
@media only screen and (max-width: 768px){
    .cont{
        grid-template-columns: 0 1fr 1fr;
        grid-template-areas: 
    "header header header"
    "sidebar content content";
    }
}